import React, { Component } from 'react';
import { XYPlot, XAxis, YAxis, HorizontalGridLines, LineSeriesCanvas, VerticalGridLines } from 'react-vis';
import '../node_modules/react-vis/dist/style.css';
import { Button, Dropdown, Statistic } from 'semantic-ui-react';


const axisStyle = {
    ticks: {
        color: '#333'
    },
    title: {
        color: '#333'
    }
};

function isMobile() {
    let check = false;
    (function (a) {
      if (/(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(a) || /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw(n|u)|c55\/|capi|ccwa|cdm|cell|chtm|cldc|cmd|co(mp|nd)|craw|da(it|ll|ng)|dbte|dcs|devi|dica|dmob|do(c|p)o|ds(12|d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(|_)|g1 u|g560|gene|gf5|gmo|go(\.w|od)|gr(ad|un)|haie|hcit|hd(m|p|t)|hei|hi(pt|ta)|hp( i|ip)|hsc|ht(c(| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i(20|go|ma)|i230|iac( ||\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|[a-w])|libw|lynx|m1w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|mcr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|([1-8]|c))|phil|pire|pl(ay|uc)|pn2|po(ck|rt|se)|prox|psio|ptg|qaa|qc(07|12|21|32|60|[2-7]|i)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h|oo|p)|sdk\/|se(c(|0|1)|47|mc|nd|ri)|sgh|shar|sie(|m)|sk0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h|v|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl|tdg|tel(i|m)|tim|tmo|to(pl|sh)|ts(70|m|m3|m5)|tx9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas|your|zeto|zte/i.test(a.substr(0, 4)))
        check = true;
    })(navigator.userAgent || navigator.vendor || window.opera);
    return check;
  }

class Plot extends Component {

    state = {
        view: 'Download',
        tickCount: Infinity,
        // data: null
    }

    toggleView = (e, data) => {
        if (this.state.view === 'Download') {
            this.setState({ view: 'Upload', data: this.props.upload });
        }
        else {
            this.setState({ view: 'Download', data: this.props.download  });
        }
    }

    setTickCount = (e, data) => {
        this.setState({tickCount: data.value})
    }

    OptionPanel = () => {
        return (
            <div>
                <Button onClick={this.toggleView} color={this.props.parentColor}>Show {this.state.view === 'Download' ? 'Upload' : 'Download'}</Button>
                <Dropdown
                    text='Data Filter'
                    icon='filter'
                    floating
                    labeled
                    button
                    className='icon'
                >
                    <Dropdown.Menu>
                        <Dropdown.Header icon='tags' content='Filter By Days' />
                        <Dropdown.Divider />
                        <Dropdown.Item
                            label={{ color: 'red', empty: true, circular: true }}
                            text='Last 24 hours'
                            value={24+1}
                            onClick={this.setTickCount}
                        />
                        <Dropdown.Item
                            label={{ color: 'blue', empty: true, circular: true }}
                            text='Last 7 days'
                            value={7*(24+1)}
                            onClick={this.setTickCount}
                        />
                        <Dropdown.Item
                            label={{ color: 'black', empty: true, circular: true }}
                            text='Last 30 days'
                            value={30*(24+1)}
                            onClick={this.setTickCount}
                        />
                    </Dropdown.Menu>
                </Dropdown>
                <Statistic size={'mini'}>
                    <Statistic.Value>Average: {this.average(this.dataReducer())} Mbps</Statistic.Value>
                </Statistic>
            </div >
        )
    }

    dataReducer = () => {
        if (this.state.tickCount === Infinity){
            return this.state.view === 'Upload' ? this.props.upload : this.props.download
        }
        else{
            let length = this.props.download.length;
            return this.state.view === 'Download' ? this.props.download.slice(length - this.state.tickCount,length) : this.props.upload.slice(length - this.state.tickCount,length)
        }
    }

    average = (data) => {
        if (data === null){
            return null
        }
        let avg = 0;
        for (let i = 0; i < data.length; i++){
            avg += data[i].y;
        }
        avg = avg / (data.length - 1)
        return Math.round(avg * 100) / 100
    }

    handleWidth = () =>{
        if (isMobile()){
            return 350
        }
        else{
            return 850
        }
    }

    handleHeight = () => {
        if (isMobile()){
            return 350
        }
        else{
            return 500
        }
    }

    render() {

        return (
            <>
                {this.OptionPanel()}
                <XYPlot 
                width={this.handleWidth()}
                height={this.handleHeight()}
                xType={"time"}
                margin={{ bottom: 100 }}
                >
                    <HorizontalGridLines />
                    <VerticalGridLines />
                    <XAxis
                        hideLine
                        tickLabelAngle={-30}
                        style={axisStyle}
                    />
                    <YAxis
                        hideLine
                        title="Speed (Mbps)"
                        style={axisStyle}
                    />
                    <LineSeriesCanvas
                        style={{ strokeWidth: 5 }}
                        curve={'curveBasis'}
                        data={this.dataReducer()}
                    />
                </XYPlot>
            </>
        )
    }
}

export default Plot;